import './intro.css';
import Me from '../../img/me.png';


const Intro = () => {
    return (
        <div className = "i">
            <div className = "i-left">
                <div className = "i-left-wrapper">
                    <h2 className = "i-intro">Hola, mi nombre es</h2>
                    <h1 className = "i-name">Alexander Fernandez</h1>
                    <div className = "i-title">
                        <div className="i-title-wrapper">
                            <div className = "i-title-item">Desarrollador Flutter</div>
                            <div className = "i-title-item">Ingeniero Automático</div>
                            <div className = "i-title-item">Python Desktop Apps</div>
                            <div className = "i-title-item">Programador Arduino</div>
                        </div>
                    </div>
                    <br />
                    <p className = "i-desc">
                        Desarrollador Flutter (1 año de Experiencia)
                    </p>
                </div>
                
            </div>
            <div className = "i-right">
                <div className="i-bg"></div>
                <img src={Me} alt=""  className = "i-img"/>
            </div>
        </div>
    )
}

export default Intro
